@import url("https://netdna.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css");

* {
  color: #F7F5D1;
  padding: 0;
  margin: 0;
}
body {
  font-size: 1em;
  display: -ms-flexbox;
  display: flex;
  font-family: "Raleway", Verdana, sans-serif;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(44,149,133,0.77)),
    to(rgba(2,82,70,0.77))
  ), url("../assets/mountain_range_pixabay.png") no-repeat center center fixed;
  background: -webkit-linear-gradient(
    left,
    rgba(44,149,133,0.77),
    rgba(2,82,70,0.77)
  ), url("../assets/mountain_range_pixabay.png") no-repeat center center fixed;
  background: -o-linear-gradient(
    left,
    rgba(44,149,133,0.77),
    rgba(2,82,70,0.77)
  ), url("../assets/mountain_range_pixabay.png") no-repeat center center fixed;
  background: linear-gradient(
    to right,
    rgba(44,149,133,0.77),
    rgba(2,82,70,0.77)
  ), url("../assets/mountain_range_pixabay.png") no-repeat center center fixed;
  background-size: cover;
  overflow: auto;
  height: 100%;
}


h1 {
  font-size: 2.3em;
  font-weight: 600;
  color: #F6E7AE;
  margin-bottom: 2vh;
}

h2 {
  font-size: 1.8em;
  font-style: italic;
  font-weight: 400;
  color: #F6E7AE;
  margin-bottom: 2vh;
}

p {
  font-size: 1.3em;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li {
  float: left;
}



